
.chatroom-rename {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
}
.chatroom-rename span {
  cursor: pointer;
  top: 50%;
  margin-top: -15px;
  position: absolute;
  left: 250px;
  background: #EEE;
  height: 30px;
  line-height: 30px;
  padding: 0 15px;
  border-radius: 2px;
  text-align: center;
}
.chatroom-rename span:hover {
  background: #ccc;
}
.chatroom-rename input {
  position: absolute;
  left: 80px;
  top: 50%;
  height: 30px;
  line-height: 30px;
  text-indent: 10px;
  border: none;
  width: 160px;
  border-radius: 3px;
  margin-top: -15px;
  outline: none;
}
.chatroom-rename label {
  position: absolute;
  top: 50%;
  margin-top: -15px;
  line-height: 30px;
  left: 10px;
  color: #FFF;
  font-size: 14px;
}



