
ul,li{ padding: 0; margin:0px;  list-style: none;
}
.chatroom {
    font-size: 12px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #FFF;
    box-shadow: -3px -2px 8px -1px rgba(0,0,0,0.2);
    border-radius: 2px 0px 0px 2px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.chatroom-fold {
    right: -404px;
    bottom: -368px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.chatroom-fold .chatroom-tribe .count {
    right: 42px;
}
.chatroom-info {
    position: absolute;
    right: 10px;
    top: 6px;
    cursor: pointer;
    z-index: 12;
}
.chatroom-fold .chatroom-info:after {
    content: '[展开]';
    color: #F40;
    z-index: 10;
    position: relative;
    top: 5px;
}
.chatroom-fold .chatroom-info {
    right: 412px;
}
.chatroom-pannels {
     display: -webkit-box;
     display: -webkit-flex;
     display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100%;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.chatroom-fold .chatroom-rename {
    display: none !important;
}
.chatroom-log-welcome {
    padding-left: 20px;
    padding-right: 20px;
}
.chatroom-log-welcome img {
    width: 28px;
    vertical-align: middle;
    margin: 0 2px;
    border-radius: 100%;
    cursor: pointer;
}
.chatroom-log-welcome strong {
    font-weight: normal;
    cursor:pointer;
    text-decoration: underline;
}



