
.chatroom-pannel-ft {
    width:100%;
    border-top: 1px #d9d9d9 solid;
    height: 40px;
    padding:5px 0px;
    display: -webkit-box;
    display: flex;
    display: -webkit-flex;
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
    -webkit-flex-direction:row;
            flex-direction:row;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-justify-content:space-around;
            justify-content:space-around;
    -webkit-box-align:center;
    -webkit-align-items:center;
            align-items:center;
    background: #f2f2f5;
}
.chatroom-pannel-ft textarea {
    resize: none;
    border: none;
    width: 70%;
    outline: none;
    line-height: 18px;
    font-size: 12px;
    overflow-y: auto;
    border:1px solid #ddd;
    border-radius: 5px;
    height: 38px;
    box-sizing:border-box;
}
.record-btn{
  color:#666;
  width: 70%;
   border:1px solid #ddd;
   border-radius: 5px;
   height: 38px;
   line-height: 35px;
  font-size: 16px;
  text-align: center;
  box-sizing:border-box;
  cursor: pointer;
}
.ractive{ background: #eee;
}
.audio-send-btn{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
    border:1px solid #ddd;
    cursor: pointer;
    font-size: 25px;
    text-align: center;
}
.audio-send-btn i{ margin-right: 0px; color: #333;
}
.audio-btn-active{
  background: #eee;
}
.chatroom-send-btn {
    width: 60px;
    height: 40px;
    background: #3AA5D4;
    text-align: center;
    line-height: 40px;
    font-size: 14px;
    cursor: pointer;
    color: #fff;
    border-radius: 5px;
}
.chatroom-send-btn:hover {
    background: #44BCED;
    -webkit-transition:all 0.3s;
    transition: all 0.3s;
}



