
.chatroom-pannel-bd {
  height: 92%;
  overflow-y: auto;
}
.chatroom-item {
  display: none;
  padding-bottom: 10px;
}
.chatroom-item.current {
  display: block;
}
.chatroom-log {
  padding: 25px 10px 0;
  overflow: hidden;
  line-height: 18px;
}
.chatroom-log .avatar {
  width: 40px;
  height: 40px;
  overflow: hidden;
  float: left;
  border-radius: 20px;
  cursor: pointer;
  border:1px solid #ddd;
}
.chatroom-log .avatar img {
  width: 40px;
  min-height: 40px;
}
.chatroom-log .time {
  width: 80%;
  margin-left: 13px;
  margin-bottom: 4px;
  float: left;
  color: #999;
}
.chatroom-log .time:after{
      content: '11';
      display: block;
      height:0;
      visibility: hidden;
      clear: both;
}
.chatroom-log .time b {
  text-decoration: underline;
  font-weight: normal;
}
.chatroom-log .time b:hover {
  cursor: pointer;
  color: #666;
}
.chatroom-log .detail {
  max-width: 222px;
  padding: 5px 9px;
  position: relative;
  margin-left: 40px;
  margin-top: -10px;
  background: #fff;
  float: left;
  border: 1px #ccc solid;
  border-radius: 5px;
  font-size: 14px;
  box-shadow: 0 3px 3px rgba(0,0,0,.05);
  word-break: break-word;
  clear: left;
}
.chatroom-log .detail:before, .chatroom-log .detail:after {
  content: " ";
  display: inline-block;
  width: 0;
  height: 0;
  border-width: 7px;
  border-style: solid;
  overflow: hidden;
  font-size: 0;
  line-height: 0;
  vertical-align: top;
  top: 10px;
  position: absolute;
  border-top-color: rgba(0, 0, 0, 0);
  border-bottom-color: rgba(0, 0, 0, 0);
  border-left-color: rgba(0, 0, 0, 0);
}
.chatroom-log .detail:before {
  border-width: 4px;
  border-top-color: #ccc;
  color: #ccc;
  left: -8px;
}
.chatroom-log .detail:after {
  border-top-color: #fff;
  color: #fff;
  left: -6px;
  border-width: 3px;
  top: 11px;
}
.chatroom-log.myself {
}
.chatroom-log.myself .avatar {
  float: right;
}
.chatroom-log.myself .time {
  float: right;
  margin-left: 0;
  margin-right: 13px;
  text-align: right;
}
.chatroom-log.myself .detail {
  float: right;
  margin-top: -10px;
  margin-right: 40px;
  background: #ABE95C;
  border: 1px #8BB055 solid;
  clear: right;
}
.myself .detail:before, .myself .detail:after {
  border-top-color: rgba(0, 0, 0, 0);
  border-bottom-color: rgba(0, 0, 0, 0);
  border-right-color: rgba(0, 0, 0, 0);
}
.chatroom-log.myself .detail:before {
  border-top-color: #8BB055;
  border-left-color: #8BB055;
  left: auto;
  right: -8px;
}
.chatroom-log.myself .detail:after {
  border-top-color: #d7ebfe;
  border-left-color: #d7ebfe;
  left: auto;
  right: -6px;
}
.chatroom-log-info {
  text-align: center;
  padding: 5px;
  color: #999;
}
.duration{
  font-size: 14px;
  position: relative;
  padding-right:8px;
  padding-left: 8px;
  cursor: pointer;
}
.duration::after{
  position: absolute;
  width: 6px;
  height: 6px;
  background: #f30;
  border-radius: 3px;
  top: 1px;
  content: " "; 
  margin-left: 5px;
}




