
.chatroom-tribe {
  -webkit-user-select: none;
  padding: 4px 5px;
  cursor: pointer;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
  margin-bottom: 1px;
  position: relative;
}
.chatroom-tribe:hover, .chatroom-tribe.current {
  background: #f2f2f5;
}
.chatroom-tribe img {
  width: 30px;
  height: 30px;
  vertical-align: middle;
}
.chatroom-tribe .name {
  max-width: 90px;
  display: inline-block;
  vertical-align: middle;
  height: 30px;
  margin-left: 10px;
  line-height: 30px;
  overflow: hidden;
}
.chatroom-tribe .count {
  min-width: 18px;
  height: 13px;
  line-height: 14px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  padding: 1px 2px;
  background: #fa7d3c;
  color: #fff;
  border-radius: 3px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  position: absolute;
  right: 22px;
  top: 12px;
}
.chatroom-tribe .iconfont {
  position: absolute;
  right: 0;
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: 12px;
  font-family: Arial;
  color: #696e78;
  font-style: normal;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  font-weight: bold;
  visibility: hidden;
}
.chatroom-tribe .iconfont:hover {
  color: #fa7d3c;
}
.chatroom-tribe:hover .iconfont {
  visibility: visible;
}
.chatroom-tribes {
  width: 100%;
  border-right: 1px #d9d9d9 solid;
  overflow: hidden;
  height: 100%;
  overflow-y: auto;
}



